/* Register.css */

.auth-form {
    max-width: 300px;
    margin-bottom: 20px;
  }
  
  .auth-input {
    margin: 8px 0;
    width: 100%;
    padding: 6px;
    box-sizing: border-box;
  }
  
  .auth-error {
    color: red;
    font-size: 0.9rem;
  }
  
  .auth-button {
    margin-top: 10px;
    padding: 6px 12px;
    cursor: pointer;
    border-color: green;
    border-radius: 5px;
  }
  